//引入路由组件
import Login from "@/pages/LOGIN/login";
import Personal from "@/pages/personal/personal";
import Tourist from "@/pages/Tourist/tourist";
import Mine from "@/pages/Mine/Mine";
import Overview from "@/pages/Community/overview";
import Composition from "@/pages/Mine/Composition";
import detailComposition from "@/pages/Mine/detailcomposition";
import Collect from "@/pages/Mine/Collect";
import Write from "@/pages/Mine/Write";
import Details from "@/pages/Community/details";
//配置路由信息
export default [
  {
    //登录路由
    path: "/login",
    component: Login,
    meta:{
      title:"博客"
    }
  },
  {
    //个人页面
    path: "/personal/:keywordone/:keywordtwo",
    component: Personal,
    meta:{
      title:"个人"
    }
  },
  {
    //博客路由(写作，文档，(收藏，点赞))
    path: "/personal/:keywordfrist/:keywordsecond/:keywordthrid",
    component: Mine,
    meta:{
      title:"文档"
    },
    children: [
      {
        //作品
        path: "compositions",
        component: Composition,
        meta:{
          title:"我的作品"
        },
      },
      {
        //收藏
        path: "collect",
        component: Collect,
        meta:{
          title:"我的收藏"
        },
      },
      {
        //写作
        path: "Write",
        component: Write,
        meta:{
          title:"写作"
        },
      },
      {
        path:"composition",
        component:detailComposition
      }
    ],
  },
  {
    //游客路由
    path: "/tourist/:keyword",
    component: Tourist,
    meta:{
      title:"游客"
    }
  },

  {
    //社区总览路由
    path: "/community/:keyword/overview",
    component: Overview,
    meta:{
      title:"社区"
    }
  },
  {
    //文章详情路由
    path: "/community/:keyword/details",
    component: Details,
    meta:{
      title:"作品"
    }
  },

  {
    //重定向，让项目一开始就固定在login路径
    path: "*",
    redirect: "/login",
  },
];
