<template>
  <div class="bodybox">
    <div class="headerbox">
      <div class="userinfo">
        你好,<span class="user_name">{{username}}</span>
      </div>
      <div class="index">
        <div class="index_1">
          <div
            class="boxone"
            @click="gocommunity"
          ><span class="iconfont icon-BasicIcons-all-6 index_tips"></span>社区</div>
        </div>
        <div class="index_2">
          <div
            class="boxone"
            @click="composition"
          ><span class="iconfont icon-BasicIcons-all-11 index_tips2"></span>文档</div>
        </div>
        <div class="index_3">
          <div
            class="boxone"
            @click="showmessage"
          ><span class="iconfont icon-BasicIcons-all-12 index_tips3"></span>留言</div>
        </div>
        <div class="search-box">
          <!-- 按键事件及按键别名 -->
          <input
            class="search-txt"
            type="text"
            placeholder="search users ..."
            v-model="others"
            @keyup.enter="searchOthers"
          />
          <a
            class="iconfont icon-BasicIcons-all- search-btn"
            href="#"
            @click="searchOthers"
          >
            <i class=" fas fa-search"></i>
          </a>
        </div>
      </div>
    </div>
    <header class="header">
    </header>
    <div class="banner">
      <img
        :src="src"
        class="bigimgs"
      />
      <span
        class="iconfont icon-yuandiancaidan"
        @click="showbackage"
        @mouseover="showTips"
        @mouseleave="hiddenTips"
      ></span>
      <div class="bannerbox">
        <div class="picturebox">
          <div class="backpicture">
            <img
              :src="picture[maths]"
              class="picture"
            >
          </div>
        </div>
        <div class="namebox">
          {{username}}
        </div>
        <div
          class="beginbox"
          @click="write"
        >
          <span class="iconfont icon-BasicIcons-all-1 down"></span>写博客
        </div>
        <div class="footerbox">
          <div class="iconfont icon-githublogo f1"></div>
          <div class="iconfont icon-yuedu f2"></div>
          <div
            class="iconfont icon-zuopin f3"
            @click="write"
          ></div>
          <div
            class="iconfont icon-shoucang f4"
            @click="collect"
          ></div>
        </div>
      </div>
    </div>
    <div class="bannerbody">
      <span class="iconfont icon-gexinghuaxin"> 个性签名</span>
      <span
        class="iconfont icon-BasicIcons-all-1"
        @mouseover="showsigntips"
        @mouseleave="hiddensigntips"
        @click="signing"
      ></span>
      <p
        class="signtips"
        v-show="signtips"
      >编辑个性签名</p>
      <textarea
        class="signing"
        :readonly="readonlys"
        @blur="nosign"
        v-model="sign"
      ></textarea>
    </div>
    <div class="footers">
      <div
        class="page1"
        @click="lookpage"
      >
        <div class="back">
          <img
            src="../../images/article8.jpeg"
            class="backage"
            v-show="articleOne.time"
          >
          <span
            class="title"
            v-show="articleOne.time"
          >{{articleOne.title}}</span>
        </div>
        <textarea
          class="content article1"
          readonly
          :value="articleOne.content"
        ></textarea>
        <span class="tips">
          <span
            class="iconfont icon-shijianchuo"
            v-show="articleOne.time"
          ></span>
          <span class="timetips">{{articleOne.time}}</span>
          <span
            class="iconfont icon-yonghu "
            v-show="articleOne.username"
          ></span>
          <span class="authortips">{{articleOne.username}}</span>
        </span>
      </div>
      <div
        class="page2"
        @click="lookpage"
      >
        <div class="back">
          <img
            src="../../images/article7.jpeg"
            class="backage"
            v-show="articleOne.time"
          >
          <span
            class="title"
            v-show="articleOne.time"
          >{{articleTwo.title}}</span>
        </div>
        <textarea
          class="content article1"
          readonly
          :value="articleTwo.content"
        ></textarea>
        <span class="tips">
          <span
            class="iconfont icon-shijianchuo"
            v-show="articleTwo.time"
          ></span>
          <span class="timetips">{{articleTwo.time}}</span>
          <span
            class="iconfont icon-yonghu"
            v-show="articleTwo.time"
          ></span>
          <span class="authortips">{{articleTwo.username}}</span>
        </span>
      </div>
      <div
        class="page3"
        @click="lookpage"
      >
        <div class="back">
          <img
            src="../../images/article5.jpeg"
            class="backage"
            v-show="articleOne.time"
          >
          <span
            class="title"
            v-show="articleOne.time"
          >{{articleThree.title}}</span>
        </div>
        <textarea
          class="content article1"
          readonly
          :value="articleThree.content"
        ></textarea>
        <span class="tips">
          <span
            class="iconfont icon-shijianchuo"
            v-show="articleThree.time"
          ></span>
          <span class="timetips">{{articleThree.time}}</span>
          <span
            class="iconfont icon-yonghu"
            v-show="articleThree.time"
          ></span>
          <span class="authortips">{{articleThree.username}}</span>
        </span>
      </div>
    </div>
    <!-- <div class="bottom">
    </div> -->
    <div
      class="imgbox"
      v-show="showbackages"
    >
      <img
        v-for="(i,index) in bigbackages"
        :key="index"
        :src="bigbackages[index]"
        class="backbox"
        @click="changebackages($event)"
      >
    </div>
    <span class="changeTips">这里可以更换背景哦!</span>
    <p
      class="board"
      v-show="showmessageBox"
    >留言板 <span
        class="iconfont icon-close"
        @click="closeBoard"
      ></span></p>
    <div
      class="messageBox"
      v-show="showmessageBox"
    >
      <div
        class="messageSmallBox"
        v-for="(i,index) in MessageDate"
        :key="index"
      >
        <span class="touristNames">{{i.touristName}}</span>
        <span class="floor">第<span>{{MessageDate.length - index}}</span>楼</span>
        <div class="messageDiv">{{i.message}}</div>
        <span class="TimeDiv">{{i.leaveTime}}</span>
        <span
          class="deleteMessage"
          @click="deleteMessage($event)"
        >删除<input
            type="text"
            :value="i.idleavemessage"
            v-show="false"
          ></span>
      </div>
    </div>
  </div>
</template>

<script>
import ajax from "../../封装Ajax函数";
import heart from "../../点击特效";
export default {
  name: "personal",
  data() {
    return {
      src: require("../../images/bgd2.jpeg"),
      username: this.$route.params.keywordone,
      articleOne: {},
      articleTwo: {},
      articleThree: {},
      signtips: false,
      sign: "",
      mocksign: "",
      readonlys: true,
      showbackages: 0,
      maths: Math.round(Math.random() * 8),
      others: "",
      picture: [
        require("../../images/article1.jpeg"),
        require("../../images/article2.jpeg"),
        require("../../images/article3.jpeg"),
        require("../../images/article4.jpg"),
        require("../../images/article5.jpeg"),
        require("../../images/article6.jpeg"),
        require("../../images/article7.jpeg"),
        require("../../images/article8.jpeg"),
      ],
      bigbackages: [
        require("../../images/1立春.jpeg"),
        require("../../images/2大寒.jpeg"),
        require("../../images/3小寒.jpeg"),
        require("../../images/4冬至.jpeg"),
        require("../../images/5大雪.jpeg"),
        require("../../images/6小雪.jpeg"),
        require("../../images/7立冬.jpeg"),
        require("../../images/8霜降.jpeg"),
        require("../../images/9寒露.jpeg"),
        require("../../images/10秋分.jpeg"),
        require("../../images/11白露.jpeg"),
        require("../../images/12处暑.jpeg"),
        require("../../images/13立秋.jpeg"),
        require("../../images/14大暑.jpeg"),
        require("../../images/15小暑.jpeg"),
        require("../../images/16夏至.jpeg"),
        require("../../images/17芒种.jpeg"),
        require("../../images/18小满.jpeg"),
        require("../../images/19立夏.jpeg"),
        require("../../images/20谷雨.jpeg"),
        require("../../images/21清明.jpeg"),
        require("../../images/22春分.jpeg"),
        require("../../images/23惊蛰.jpeg"),
        require("../../images/24雨水.jpeg"),
      ],
      document: [],
      MessageDate: [],
      showmessageBox: 0,
    };
  },
  mounted() {
    heart();
    let pages = document.querySelector(".footers").querySelectorAll("div");
    window.addEventListener("scroll", function () {
      if (!window.pageYOffset) {
        document.querySelector(".header").style.backgroundColor = "";
      } else {
        //透明度
        document.querySelector(".header").style.opacity =
          window.pageYOffset / 800;
      }
      if (window.pageYOffset >= 200) {
        for (let i = 0; i < pages.length; i++) {
          pages[i].style.display = "block";
        }
      } else {
        for (let i = 0; i < pages.length; i++) {
          pages[i].style.display = "none";
        }
      }
    });

    this.check(this.username);
    this.getinfo(this.username);
    this.getData(this.username);
    this.getsign(0);
    this.showchangeTips();
  },
  methods: {
    showbackage() {
      if (this.showbackages) {
        this.showbackages = 0;
      } else {
        this.showbackages = 1;
      }
    },
    //更换背景图片
    changebackages(e) {
      this.src = e.target.src;
    },
    getsign(n) {
      var timer = setInterval(() => {
        this.sign += this.mocksign[n];
        n++;
        if (n >= this.mocksign.length) {
          clearInterval(timer);
        }
      }, 200);
    },
    write() {
      //在新窗口打开博客页面
      const { href } = this.$router.resolve({
        path: `/personal/${this.$route.params.keywordone}/${this.$route.params.keywordtwo}/Mine/write`,
        query: {id:1},
      });
      window.open(href, "_blank");
    },
    composition() {
      const { href } = this.$router.resolve({
        path: `/personal/${this.$route.params.keywordone}/${this.$route.params.keywordtwo}/Mine/composition`,
        query: {id:2},
      });
      window.open(href, "_blank");
    },
    collect() {
      const { href } = this.$router.resolve({
        path: `/personal/${this.$route.params.keywordone}/${this.$route.params.keywordtwo}/Mine/collect`,
        query: {id:3},
      });
      window.open(href, "_blank");
    },
    //从地址栏输入地址访问校验
    check(username) {
      let key = this.$route.params.keywordtwo;
      ajax({
        type: "get",
        url: "http://127.0.0.1:8000/check",
        data: {
          username,
          key,
        },
        success: (result) => {
          alert(result);
          window.stop();
        },
        error: () => {},
      });
    },
    //获取文章
    getData(username) {
      ajax({
        type: "get",
        url: "http://127.0.0.1:8000/articleAll",
        data: {
          username,
        },
        success: (result) => {
          result = eval("(" + result + ")");
          for (let i = 0; i <= result.length - 1; i++) {
            let str = result[i].content;
            while (str.indexOf("<br/>") != -1) {
              str = str.replace("<br/>", "\n");
            }
            result[i].content = str;
          }
          if (result.length >= 3) {
            this.articleOne = result[result.length - 1];
            this.articleTwo = result[result.length - 2];
            this.articleThree = result[result.length - 3];
          } else if (result.length == 2) {
            this.articleOne = result[result.length - 1];
            this.articleTwo = result[result.length - 2];
          } else if (result.length == 1) {
            this.articleOne = result[result.length - 1];
          }
        },
        error: function () {},
      });
    },
    //获取个人信息
    getinfo(username) {
      ajax({
        type: "get",
        url: "http://127.0.0.1:8000/info",
        data: {
          username,
        },
        success: (result) => {
          let user = eval("(" + result + ")");
          this.mocksign = user[0].sign;
        },
      });
    },
    showsigntips() {
      this.signtips = true;
    },
    hiddensigntips() {
      this.signtips = false;
    },
    signing() {
      this.readonlys = false;
      document.querySelector(".signing").focus();
    },
    nosign() {
      this.readonlys = true;
      let sign = this.sign;
      let username = this.$route.params.keywordone;
      ajax({
        type: "get",
        url: "http://127.0.0.1:8000/sign",
        data: {
          username,
          sign,
        },
        success: () => {},
        error: function () {},
      });
    },
    searchOthers() {
      let username = this.others;
      let p = 0;
      if (username == this.username) {
        alert("不能搜索自己!");
        this.others = "";
        return;
      }
      ajax({
        type: "get",
        url: "http://127.0.0.1:8000/username",
        data: {
          username,
          p,
        },
        success: (result) => {
          let user = eval("(" + result + ")");
          if (user.p == 1) {
            alert("用户不存在");
          } else {
            const { href } = this.$router.resolve({
              path: `/tourist/${this.others}`,
              query: { tourist: this.username },
            });
            window.open(href, "_blank");
          }
          this.others = "";
        },
      });
    },
    gocommunity() {
      const { href } = this.$router.resolve({
        path: `/community/${this.$route.params.keywordone}/overview`,
        query: "",
      });
      window.open(href, "_blank");
    },
    godocument() {
      this.$refs.document.style.display = "block";
    },
    lookpage() {
      const { href } = this.$router.resolve({
        path: `/personal/${this.$route.params.keywordone}/${this.$route.params.keywordtwo}/Mine/composition`,
        query: {id:2},
      });
      window.open(href, "_blank");
    },
    showchangeTips() {
      let obj = document.querySelector(".changeTips");
      obj.style.opacity = 0;
      setTimeout(() => {
        obj.style.opacity = 1;
        let objTimer = setInterval(() => {
          obj.style.opacity -= 0.03;
          if (obj.style.opacity <= 0) {
            clearInterval(objTimer);
          }
        }, 30);
      }, 500);
    },
    showTips() {
      document.querySelector(".changeTips").style.opacity = 1;
    },
    hiddenTips() {
      document.querySelector(".changeTips").style.opacity = 0;
    },
    //获取留言
    getMessage(username) {
      ajax({
        type: "get",
        url: "http://127.0.0.1:8000/getMessage",
        data: {
          username,
        },
        success: (result) => {
          result = eval("(" + result + ")");
          for (let i = 0; i <= result.length - 1; i++) {
            let str = result[i].message;
            while (str.indexOf("<br/>") != -1) {
              str = str.replace("<br/>", "\n");
            }
            result[i].message = str;
          }
          this.MessageDate = result.reverse();
        },
        error: () => {},
      });
    },
    deleteMessage(e) {
      let username = this.username;
      let id = e.target.lastElementChild.value;
      ajax({
        type: "get",
        url: "http://127.0.0.1:8000/deleteMessage",
        data: {
          username,
          id,
        },
        success: () => {
          this.getMessage(this.username);
        },
        error: () => {},
      });
      // console.log(e.target.lastElementChild.value);
    },
    showmessage() {
      if (!this.showmessageBox) {
        this.showmessageBox = 1;
        this.getMessage(this.username);
        window.scroll(0, -window.pageYOffset);
      } else {
        this.showmessageBox = 0;
      }
    },
    closeBoard() {
      this.showmessageBox = 0;
    },
  },
};
</script>

