.footers {
  display: flex;
  margin: 565px auto 0 auto;
  width: 1200px;
  height: 302px;
  flex-direction: row;
  cursor: pointer;
  .title {
    position: relative;
    top: -59px;
    display: block;
    z-index: 999;
    height: 50px;
    // font-size: 25px;
    font: 25px "microsoft YaHei";
