<template>
  <div>
    <div class="Write">
      <input
        type="text"
        class="titles"
        placeholder="一个好的标题更容易引人注意!"
        v-model="pagetitle"
      />
      <textarea class="writeArea" v-model="pagecontent"></textarea>
      <input
        type="radio"
        id="communityTips"
        :checked="community"
        @click="ischecked"
      />
      <label for="communityTips">发送到社区</label>
      <input
        type="submit"
        class="Writesub"
        value="上传"
        @click="writting"
      />
    </div>
  </div>
</template>

<script>
import ajax from "../../封装Ajax函数";
export default {
  name: "",
  data() {
    return {
      pagetitle: "",
      pagecontent: "",
      timenow: "",
      username: "",
      community: 1,
    };
  },
  mounted() {
  },
  methods: {
    getTimeNow() {
      var date = new Date();
      function time() {
        var year = date.getFullYear();
        var month = date.getMonth() + 1;
        month = month < 10 ? "0" + month : month;
        var day = date.getDate();
        day = day < 10 ? "0" + day : day;
        var h = date.getHours();
        h = h < 10 ? "0" + h : h;
        var m = date.getMinutes();
        m = m < 10 ? "0" + m : m;
        return year + "-" + month + "-" + day + " " + h + ":" + m;
      }
      this.timenow = time()
    },
    ischecked() {
      this.community == 1?this.community = 0:this.community = 1
    },
    writting() {
      this.getTimeNow()
      let username = this.$route.params.keywordfrist
      let title = this.pagetitle
      let content = this.pagecontent.replace(/<(.+?)>/gi, "&lt;$1&gt;").replace(/&nbsp/gi, "&nbsp;").replace(/\n/gi, "<br/>")
      let timenow = this.timenow
      let community = this.community
      if(!content||!title){
        alert('标题或内容不能为空')
        this.pagetitle = this.pagecontent = ''
        return;
      }
      ajax({
        type: "get",
        url: "http://127.0.0.1:8000/article",
        data: {
          username,
          title,
          content,
          timenow,
          community
        },
        success:  (result) =>{
          alert(result)
          this.pagetitle = this.pagecontent = ''
        },
        error: function () {},
      });
    },
  },
};
</script>

<style scoped lang="less">
.titles {
  font: 17px "microsoft YaHei";
  outline: none;
  width: 780px;
  height: 30px;
  margin-bottom: 5px;
}
.writeArea {
  // width: 792px;
  font: 15px "microsoft YaHei";
  width: 782px;
  height: 600px;
  outline: none;
  resize: none;
}
.Writesub {
  position: relative;
  left: 626px;
  width: 60px;
  height: 30px;
  cursor: pointer;
  border: 1px solid #ccc;
  background-color: #fff;
  &:hover {
    background-color: #ccc;
  }
}
</style>