<template>
  <div class="textBigBox">
    <div class="textareas">
      <div class="textheader">{{TextDate[0].title}}</div>
      <div class="textfootertop"><span class="spanTime">{{TextDate[0].username}}</span></div>
      <div>
        <textarea
          class="textbanner"
          readonly
          :value="TextDate[0].content"
        ></textarea>
        <div class="textfooter"><span
            class="iconfont icon-dianzan_kuai"
            @click="dianzan($event)"
            @mouseover="likestips"
            @mouseleave="unLikestips"
          >
          </span><span
            class="iconfont icon-shoucang1"
            @click="collect($event)"
          ></span></div>
      </div>
    </div>
    <span class="likesInfo">点赞成功</span>
    <span
      class="likestips"
      v-show="likestip"
    >可以多次点赞哦!</span>
    <span
      class="collecttips"
      v-show="collecttips"
    >收藏成功!</span>
    <span
      class="collecttips cancle"
      v-show="canclecollect"
    >
      取消收藏!
    </span>
  </div>
</template>

<script>
import ajax from "../../封装Ajax函数";
export default {
  name: "Details",
  data() {
    return {
      dataId: this.$route.query.id,
      username: this.$route.params.keyword,
      TextDate: [
        {
          title: "",
          content: "",
          username: "",
          time: "",
        },
      ],
      likestip: 0,
      collecttips: 0,
      canclecollect: 0,
    };
  },
  mounted() {
    this.getText(this.dataId);
    this.collectInfo(this.username);
    // setTimeout(()=>{
    //   console.log(this.TextDate[0].title);
    //   console.log(this.TextDate[0].content);
    //   console.log(this.TextDate[0].username);
    //   console.log(this.TextDate[0].time);
    // },100)
  },
  methods: {
    getText(dataId) {
      ajax({
        type: "get",
        url: "http://127.0.0.1:8000/articleById",
        data: {
          dataId,
        },
        success: (result) => {
          result = eval("(" + result + ")");
          for (let i = 0; i <= result.length - 1; i++) {
            let str = result[i].content;
            while (str.indexOf("<br/>") != -1) {
              str = str.replace("<br/>", "\n");
            }
            result[i].content = str;
          }
          this.TextDate = result;
        },
        error: function () {},
      });
    },
    getTextLikes(dataId) {
      ajax({
        type: "get",
        url: "http://127.0.0.1:8000/articleLikes",
        data: {
          dataId,
        },
        success: () => {},
        error: function () {},
      });
    },
    dianzan() {
      //点赞
      clearInterval(likesTime);
      this.getTextLikes(this.dataId);
      let liketip = document.querySelector(".likesInfo");
      liketip.style.opacity = 1;
      var likesTime = setInterval(() => {
        liketip.style.opacity -= 0.05;
        if (liketip.style.opacity < 0) {
          clearInterval(likesTime);
        }
      }, 30);
    },
    likestips() {
      this.likestip = 1;
    },
    unLikestips() {
      this.likestip = 0;
    },
    collect(e) {
      let dataId = this.dataId;
      let username = this.username;
      let author = this.TextDate[0].username
      let time = this.TextDate[0].time
      let title = this.TextDate[0].title
      let content = this.TextDate[0].content.replace(/<(.+?)>/gi, "&lt;$1&gt;").replace(/&nbsp/gi, "&nbsp;").replace(/\n/gi, "<br/>")
      if (e.target.style.color == "rgb(8, 175, 241)") {
        //取消收藏
        e.target.style.color = "rgb(0, 0, 0)";
        ajax({
          type: "get",
          url: "http://127.0.0.1:8000/canclecollect",
          data: {
            dataId,
            username,
          },
          success: () => {
            this.canclecollect = 1;
            let obj = document.querySelector(".cancle");
            clearInterval(moveTime);
            obj.style.opacity = 1;
            var moveTime = setInterval(() => {
              obj.style.opacity -= 0.05;
              if (obj.style.opacity < 0) {
                clearInterval(moveTime);
                 this.canclecollect = 0;
              }
            }, 30);
          },
          error: function () {},
        });
      } else {
        //收藏
        e.target.style.color = "rgb(8, 175, 241)";
        ajax({
          type: "get",
          url: "http://127.0.0.1:8000/collect",
          data: {
            dataId,
            username,
            author,
            content,
            time,
            title
          },
          success: () => {
            this.collecttips = 1;
            let obj = document.querySelector(".collecttips");
            clearInterval(moveTime);
            obj.style.opacity = 1;
            var moveTime = setInterval(() => {
              obj.style.opacity -= 0.05;
              if (obj.style.opacity < 0) {
                clearInterval(moveTime);
                this.collecttips = 0;
              }
            }, 30);
          },
          error: function () {},
        });
      }
    },
    collectInfo(name) {
      ajax({
        type: "get",
        url: "http://127.0.0.1:8000/collectInfo",
        data: {
          name,
        },
        success: (result) => {
          result = eval("(" + result + ")");
          for (let i = 0; i < result.length; i++) {
            if (result[i].pageId == this.dataId) {
              document.querySelector(".icon-shoucang1").style.color =
                "rgb(8, 175, 241)";
            }
          }
        },
        error: function () {},
      });
    },
  },
};
</script>

<style scoped lang="less">
.textareas {
  position: relative;
  margin: 10px auto;
  width: 850px;
  height: 867px;
  border: 1px solid #ccc;
  .textheader {
    outline: none;
    border: none;
    width: 850px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    font: 20px "microsoft YaHei";
  }
  .textbanner {
    resize: none;
    outline: none;
    width: 846px;
    height: 770px;
    font: 15px "microsoft YaHei";
    border: none;
    border-bottom: 1px solid #ccc;
  }
  .textfootertop {
    font: 12px "microsoft YaHei";
    width: 850px;
    height: 30px;
    border-bottom: 1px solid #ccc;
    .spanTime {
      margin: 0 20px 0 500px;
    }
  }
  .textfooter {
    position: absolute;
    bottom: 0;
    width: 850px;
    height: 31px;
    line-height: 31px;
    background-color: rgb(230, 230, 230);
    .iconfont {
      font-size: 25px;
      cursor: pointer;
    }
    .icon-dianzan_kuai {
      margin: 0 50px 0 687px;
    }
  }
}
.likesInfo {
  position: relative;
  top: -57px;
  left: 1201px;
  width: 64px;
  height: 20px;
  font-size: 10px;
  opacity: 0;
}
.likestips {
  position: relative;
  top: -18px;
  left: 1075px;
  font-size: 11px;
}
.collecttips {
  font-size: 26px;
  position: relative;
  top: -815px;
  left: 844px;
}
.cancle{
  position: relative;
  top: -815px;
  left: 844px;
}
</style>