<template>
  <div>
    <div class="communityBox">
      <!-- <router-view></router-view> -->
      <div
        class="communitysmallbox"
        v-for="i in pagedata"
        :key="i.id"
      >
        <span class="communitytitle">{{i.title}}</span>
        <span
          class="communityId"
          style="display:none"
        >{{i.id}}</span>
        <div
          class="communitycontent"
          @click="goDetails($event)"
        >{{i.content}}</div>
        <div class="infoSpan">
          <span class="iconfont icon-dianzan"></span>
          <span class="Likesnum">{{i.likes}}</span>
          <span class="communitytime">{{i.time}}</span>
          <span class="Comauthor">{{i.username}}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ajax from "../../封装Ajax函数";
export default {
  name: "Overview",
  data() {
    return {
      community: 1,
      pagedata: [],
    };
  },
  mounted() {
    this.getData(this.community);
  },
  methods: {
    getData(community) {
      ajax({
        type: "get",
        url: "http://127.0.0.1:8000/articleCom",
        data: {
          community,
        },
        success: (result) => {
          result = eval("(" + result + ")");
          for (let i = 0; i <= result.length - 1; i++) {
            let str = result[i].content;
            while (str.indexOf("<br/>") != -1) {
              str = str.replace("<br/>", "\n");
            }
            result[i].content = str;
          }
          this.pagedata = result.reverse();
          console.log(result);
        },
        error: function () {},
      });
    },
    goDetails(e) {
      const { href } = this.$router.resolve({
        path: `/community/${this.$route.params.keyword}/details`,
        query: {id:e.path[1].childNodes[1].innerHTML},
      });
      window.open(href, "_blank");
    },
  },
};
</script>

<style scoped lang="less">
.communityBox {
  position: relative;
  margin: 100px auto;
  width: 600px;
  .communitysmallbox {
    margin: 10px;
    width: 500px;
    height: 110px;
    border-bottom: 1px solid #ccc;
    &:hover {
      background-color: rgb(236, 234, 234);
    }
    .communitytitle{
      font-weight: 800;
    }
    .communitycontent {
      width: 460px;
      height: 67px;
      margin-left: 10px;
      overflow: hidden;
      overflow: hidden;
      cursor: pointer;
      text-overflow: ellipsis;
      text-indent: 2em;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 3;
    }
    .communitytime {
      margin-left: 250px;
      font-size: 12px;
    }
    .Comauthor {
      font-size: 15px;
      margin-left: 15px;
    }
    .icon-dianzan{
      font-size: 19px;
    }
    .Likesnum{
      display: inline-block;
      width: 28px;
      margin-left: 5px;
    }
  }
}
</style>