<template>
  <div>
    <p class="TitleP">{{TextDates[0].title}}</p>
    <textarea readonly :value="TextDates[0].content" class="detailecom"></textarea>
  </div>
</template>

<script>
import ajax from "../../封装Ajax函数";
export default {
  name: "",
  data() {
    return {
      dataId: this.$route.query.id,
      TextDates:[{
        title:"",
        content:""
      }]
    };
  },
  mounted() {
    this.GetPage(this.dataId)
  },
  methods: {
    GetPage(dataId) {
      ajax({
        type: "get",
        url: "http://127.0.0.1:8000/articleById",
        data: {
          dataId,
        },
        success: (result) => {
          result = eval("(" + result + ")");
          for (let i = 0; i <= result.length - 1; i++) {
            let str = result[i].content;
            while (str.indexOf("<br/>") != -1) {
              str = str.replace("<br/>", "\n");
            }
            result[i].content = str;
          }
          this.TextDates = result;
        },
        error: function () {},
      });
    },
  },
};
</script>

<style scoped lang="less">
.TitleP{
  width: 750px;
  font-weight: 800;
  text-align: center;
}
.detailecom{
  resize: none;
  width: 750px;
  height: 600px;
  font-size: 15px;
}
</style>