import {reqGetArticle} from '@/API'
const state = {
    article:{}
}
const mutations = {
    GETARTICLELIST(state,articlelist){
        state.articlelist = articlelist
    }
}
const actions = {
    async getArticleList({commit},params={}){
        let result = await reqGetArticle(params)
        if(result.code == 200){
            commit('GETARTICLELIST',result.data)
        }
    }
}
const getters = {}

export default{
    state,
    mutations,
    actions,
    getters
}