<template>
  <div class="all">
    <canvas class="canvasbox"></canvas>
    <p class="name_tips"></p>
    <p class="password_tips"></p>
    <p class="login_tips"></p>
    <div class="register">
      <span>用户名:</span><input
        type="text"
        class="name"
        @blur="get_username"
        @focus="inner($event)"
      />
      <span>密码:</span><input
        type="password"
        class="pwd"
      /><span
        class="iconfont icon-jurassic_loseeyes eyes reeyes"
        @click="open"
        :style="{display:eyes}"
      ></span><span
        class="iconfont icon-jurassic_openeyes eyes reeyes eyes_close"
        :style="{display: eyesclose}"
        @click="close"
      ></span><span>再次确认:</span><input
        type="password"
        class="pwdagain"
        @blur="aline"
      />
      <input
        type="button"
        class="sub"
        value="注册"
        @click="register"
      />
      <div class="login">
        <span>用户名:</span><input
          type="text"
          class="login_name"
          ref="login_name"
        />
        <span>密码:</span><input
          type="password"
          class="login_pwd"
          ref="login_pwd"
          @keyup.enter="login"
        /><span
          class="iconfont icon-jurassic_loseeyes login_eyes"
          @click="openeyes"
          :style="{display:eyes}"
        ></span>
        <span
          class="iconfont icon-jurassic_openeyes login_eyes login_eyes_close"
          :style="{display: eyesclose}"
          @click="closeeyes"
        ></span>
        <input
          type="button"
          class="sub login_sub"
          value="登录"
          @click="login"
        />
      </div>
    </div>
    <div class="mask">
      <div class="imags">
        <img
          :src="src1"
          class="image_nan"
          :style="{display:bag_nan}"
        />
        <img
          :src="src"
          class="image_nv"
          :style="{display:bag_nv}"
        />
      </div>
      <span class="reg">没有账号,去<a
          href="javascript:;"
          @click="leftmove"
        >注册!</a></span>
      <span
        style="display: none"
        class="log"
      >已有账号,去<a
          href="javascript:;"
          @click="rightmove"
        >登录!</a></span>
    </div>
  </div>
</template>

<script>
import 'bcryptjs'
import move from "../../animate";
import ajax from "../../封装Ajax函数";
import e from "./xinkon/script";
// import bcry from 'bcryptjs'
export default {
  name: "LOGIN",
  data() {
    return {
      src1: require("../../images/夜晚1.gif"),
      src: require("../../images/夜晚.gif"),
      eyes: "block",
      eyesclose: "none",
      bag_nv: "block",
      bag_nan: "none",
      name_key: 0,
      password_key: 0,
    };
  },
  mounted() {
      e()
  },
  methods: {
    open() {
      this.eyes = "none";
      this.eyesclose = "block";
      document.querySelector(".pwd").type = "text";
      document.querySelector(".pwdagain").type = "text";
    },
    close() {
      this.eyes = "block";
      this.eyesclose = "none";
      document.querySelector(".pwd").type = "password";
      document.querySelector(".pwdagain").type = "password";
    },
    openeyes() {
      this.eyes = "none";
      this.eyesclose = "block";
      document.querySelector(".login_pwd").type = "text";
    },
    closeeyes() {
      this.eyes = "block";
      this.eyesclose = "none";
      document.querySelector(".login_pwd").type = "password";
    },
    leftmove() {
      move(document.querySelector(".mask"), 970, function () {});
      document.querySelector(".reg").style.display = "none";
      document.querySelector(".log").style.display = "block";
      this.bag_nv = "none";
      this.bag_nan = "block";
      //    document.querySelector('.login_name').value = ''
      this.$refs.login_name.value = "";
      this.$refs.login_pwd.value = "";
      document.querySelector(".name_tips").value = "";
      document.querySelector(".password_tips").value = "";
    },
    rightmove() {
      move(document.querySelector(".mask"), 572, function () {});
      document.querySelector(".reg").style.display = "block";
      document.querySelector(".log").style.display = "none";
      this.bag_nv = "block";
      this.bag_nan = "none";
      document.querySelector(".name").value = "";
      document.querySelector(".pwd").value = "";
      document.querySelector(".pwdagain").value = "";
    },
    get_username() {
      let name_tips = document.querySelector(".name_tips");
      let username1 = document.querySelector(".name");
      // let btn = document.querySelector('.btn')
      let username = username1.value;
      let p = 0;
      ajax({
        type: "get",
        url: "http://127.0.0.1:8000/username",
        data: {
          username,
          p,
        },
        success: (result) => {
          let user = eval("(" + result + ")");
          if (user.p == 0) {
            name_tips.style.color = "red";
            name_tips.innerHTML = "用户名不能为空";
            username1.style.borderColor = "red";
          }
          if (user.p == 1) {
            name_tips.style.color = "green";
            name_tips.innerHTML = "用户名有效";
            this.name_key = 1;
          }
          if (user.p == 2) {
            name_tips.style.color = "red";
            username1.style.borderColor = "red";
            name_tips.innerHTML = "用户名已存在";
          }
        },
        error: function () {},
      });
    },
    inner() {
      document.querySelector(".name_tips").innerHTML = "";
      document.querySelector(".name").style.borderColor = "skyblue";
    },
    login() {
      let login_name = document.querySelector(".login_name");
      let login_pwd = document.querySelector(".login_pwd");
      let login_tips = document.querySelector(".login_tips");
      let username = login_name.value;
      let password = login_pwd.value;
      let p = 0;
      let key = 0
      ajax({
        type: "get",
        url: "http://127.0.0.1:8000/login",
        data: {
          key,
          username,
          password,
          p,
        },
        success: (result) => {
          let user = eval("(" + result + ")");
          if (user.p == 0) {
            login_tips.style.color = "red";
            login_name.style.borderColor = "red";
            login_pwd.style.borderColor = "red";
            login_tips.innerHTML = "该用户不存在!";
          }
          if (user.p == 1) {            
            login_tips.style.color = "green";
            login_name.style.borderColor = "skyblue";
            login_pwd.style.borderColor = "skyblue";
            login_tips.innerHTML = "登录成功!";
            this.$router.replace(`/personal/${username}/${user.key}`);
          }
          if (user.p == 2) {
            login_tips.style.color = "red";
            login_name.style.borderColor = "red";
            login_pwd.style.borderColor = "red";
            login_tips.innerHTML = "用户名或密码错误!";
          }
        },
        error: function () {},
      });
    },
    aline() {
      let pwd = document.querySelector(".pwd");
      let pwdagain = document.querySelector(".pwdagain");
      if (pwdagain.value.trim() != pwd.value.trim()) {
        document.querySelector(".password_tips").innerHTML =
          "两次输入的密码不一样";
        pwdagain.style.borderColor = "red";
      } else {
        document.querySelector(".password_tips").innerHTML = "";
        pwdagain.style.borderColor = "skyblue";
        this.password_key = 1;
      }
    },
    register() {
      if (this.name_key && this.password_key) {
        let username1 = document.querySelector(".name");
        let pwd = document.querySelector(".pwd");
        let pwdtwo = document.querySelector(".pwdagain");
        let username = username1.value;
        let password = pwd.value;
        ajax({
          type: "get",
          url: "http://127.0.0.1:8000/server",
          data: {
            username,
            password,
          },
          success: function (result) {
            alert(result);
            username1.value = pwd.value = pwdtwo.value = "";
            document.querySelector(".name_tips").innerHTML = "";
            document.querySelector(".password_tips").innerHTML = "";
          },
          error: function () {},
        });
      } else {
        alert("注册失败");
      }
    },
  },
  watch: {},
};
</script>

<style lang="less" scoped>
// @import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');
