<template>
  <div>
    <div
      class="collect"
      v-for="(i,index) in collects"
      :key="index"
    >
      <span class="ColTitle">{{i.title}}</span>
      <div class="ColCon" @click="SearchCollect($event)">{{i.content}}<input
          type="text"
          :value="i.pageId"
          v-show="false"
        ></div>
      <span class="ColTime">{{i.time}}</span>
      <span class="ColAut">{{i.author}}</span>
    </div>
  </div>
</template>

<script>
import ajax from "../../封装Ajax函数";
export default {
  name: "",
  data() {
    return {
      username: this.$route.params.keywordfrist,
      collects: [],
    };
  },
  mounted() {
    this.getComposition(this.username);
  },
  methods: {
    getComposition(name) {
      ajax({
        type: "get",
        url: "http://127.0.0.1:8000/collectInfo",
        data: {
          name,
        },
        success: (result) => {
          result = eval("(" + result + ")");
          for (let i = 0; i <= result.length - 1; i++) {
            let str = result[i].content;
            while (str.indexOf("<br/>") != -1) {
              str = str.replace("<br/>", "\n");
            }
            result[i].content = str;
          }
          this.collects = result.reverse();
        },
        error: () => {},
      });
    },
    SearchCollect(e) {
      let ids = e.target.lastElementChild.value;
      const { href } = this.$router.resolve({
        path: `/community/${this.$route.params.keywordfrist}/details`,
        query: { id: ids },
      });
      window.open(href, "_blank");
    },
  },
};
</script>

<style scoped lang="less">
.collect {
  width: 780px;
  height: 120px;
  border-bottom: 1px solid #ccc;
  .ColTitle{
    margin-left: 40px;
    font-weight: 800;
    font-size: 18px;
  }
  .ColCon{
    width: 700px;
    height: 65px;
    margin-left: 55px;
    text-indent: 2em;
    overflow: hidden;
    cursor: pointer;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
  }
  .ColAut{
    margin-left: 500px;
  }
  .ColTime{
    margin-left: 40px;
    width: 114px;
    display: inline-block;
    font-size: 14px;
    font-weight: 250;
  }
}
</style>