<template>
  <div>
    <div
      class="composition"
      v-for="(i,index) in compositions"
      :key="index"
    >
      <span class="comTitle">{{i.title}}</span>
      <div
        class="comCon"
        @click="Seepage($event)"
      >{{i.content}}
      <input
          type="text"
          :value="i.id"
          v-show="false"
        >
      </div>
      <span class="comTime">{{i.time}}</span>
      <span
        class="delCom"
        @click="delCom($event)"
      >删除<input
          type="text"
          :value="i.id"
          v-show="false"
        ></span>
    </div>
  </div>
</template>

<script>
import ajax from "../../封装Ajax函数";
export default {
  name: "",
  data() {
    return {
      username: this.$route.params.keywordfrist,
      compositions: [],
    };
  },
  mounted() {
    this.getComposition(this.username);
  },
  methods: {
    getComposition(username) {
      ajax({
        type: "get",
        url: "http://127.0.0.1:8000/articleAll",
        data: {
          username,
        },
        success: (result) => {
          result = eval("(" + result + ")");
          for (let i = 0; i <= result.length - 1; i++) {
            let str = result[i].content;
            while (str.indexOf("<br/>") != -1) {
              str = str.replace("<br/>", "\n");
            }
            result[i].content = str;
          }
          this.compositions = result.reverse();
        },
        error: () => {},
      });
    },
    delCom(e) {
      let id = e.target.lastElementChild.value;
      ajax({
        type: "get",
        url: "http://127.0.0.1:8000/deleteArticle",
        data: {
          id,
        },
        success: () => {
          this.getComposition(this.username);
        },
        error: () => {},
      });
    },
    Seepage(e) {
      let ids = e.target.lastElementChild.value;
      this.$router.push({
        path:`/personal/${this.$route.params.keywordfrist}/${this.$route.params.keywordsecond}/Mine/composition`,
        query:{id:ids}
      });
    },
  },
};
</script>

<style scoped lang="less">
.composition {
  width: 780px;
  height: 120px;
  border-bottom: 1px solid #ccc;
  .comTitle {
    font-weight: 800;
    font-size: 18px;
  }
  .comCon {
    width: 700px;
    height: 65px;
    margin-left: 15px;
    text-indent: 2em;
    overflow: hidden;
    cursor: pointer;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
  }
  .comTime {
    width: 114px;
    display: inline-block;
    font-size: 14px;
    font-weight: 250;
  }
  .delCom {
    margin-left: 600px;
    font-size: 14px;
    &:hover {
      cursor: pointer;
      color: rgb(226, 76, 76);
    }
  }
}
</style>