<template>
  <div>
    <div class="Mineheader">
      <div class="routebox" ref="routebox">
        <div
          @click="goWrite"
          class="goWrite"
        >写作</div>
        <div
          @click="goComposition"
          class="goComposition"
        >我的作品</div>
        <div
          @click="goCollect"
          class="goCollect"
        >我的收藏</div>
      </div>
    </div>
    <div class="Minebanner">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import heart from "../../点击特效";
export default {
  data() {
    return {};
  },
  mounted() {
    heart();
    if(this.$route.query.id == 1){
      this.goWrite()
    }else if(this.$route.query.id == 2){
      this.goComposition()
    }else if(this.$route.query.id == 3){
      this.goCollect()
    }
  },
  methods: {
    goWrite() {
      this.$router.push(
        `/personal/${this.$route.params.keywordfrist}/${this.$route.params.keywordsecond}/Mine/write`
      );
      this.highlight()
      document.querySelector('.goWrite').style.backgroundColor = '#ccc'
    },
    goComposition() {
      this.$router.push(
        `/personal/${this.$route.params.keywordfrist}/${this.$route.params.keywordsecond}/Mine/compositions`
      );
      this.highlight()
      document.querySelector('.goComposition').style.backgroundColor = '#ccc'
    },
    goCollect() {
      this.$router.push(
        `/personal/${this.$route.params.keywordfrist}/${this.$route.params.keywordsecond}/Mine/collect`
      );
      this.highlight()
      document.querySelector('.goCollect').style.backgroundColor = '#ccc'
    },
    highlight(){
      document.querySelector('.goWrite').style.backgroundColor = 'white'
      document.querySelector('.goComposition').style.backgroundColor = 'white'
      document.querySelector('.goCollect').style.backgroundColor = 'white'
    },
  },
};
</script>

<style scoped lang="less">
.Mineheader {
  margin: 80px auto 0 -8px;
  width: 1903px;
  height: 80px;
  // background-color: red;
  .routebox {
    display: flex;
    margin: 0 auto;
    width: 600px;
    height: 80px;
    // background-color: green;
    text-align: center;
    line-height: 80px;
    cursor: pointer;
    .goWrite {
      flex: 1;
      border: 1px solid #ccc;
      margin-right: -1px;
      &:hover {
        animation: shows 0.3s ease;
      }
    }
    .goComposition {
      flex: 1;
      border: 1px solid #ccc;
      margin-right: -1px;
      &:hover {
        animation: shows 0.3s ease;
      }
    }
    .goCollect {
      flex: 1;
      border: 1px solid #ccc;
      &:hover {
        animation: shows 0.3s ease;
      }
    }
  }
}
.Minebanner {
  position: relative;
  margin: 0 auto;
  width: 800px;
  height: 750px;
  overflow: auto;
  // background-color: green;
}
@keyframes shows {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
  }
}
</style>